@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");

body {
	padding: 0;
	margin: 0;
	font-family: "Inter", sans-serif;
	box-sizing: border-box;
}

header {
	text-align: center;
	margin: 0 auto;
	font-size: 14px;
	font-weight: 500;
	width: 94%;
	max-width: 375px;
	padding-top: 1rem;
	padding-bottom: 2rem;
}

#appTitle {
	color: #3730a3;
	padding-bottom: 1rem;
}

#appTitle #appTitleContainer {
	margin: 0 auto;
	width: 185px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

#appIntro {
	color: #818cf8;
	padding-left: 0.7em;
	padding-right: 0.7em;
	font-weight: 400;
	line-height: 1.2em;
}

.textDiv {
	margin: 0 auto;
	border: 1px solid red;
}

#textDivTitle {
	font-size: 14px;
	color: #374151;
	margin-bottom: 0.5em;
}

#regSpan {
	font-weight: 500;
}

#resetSpan {
	float: right;
	font-size: 12px;
	color: #1d4ed8;
	padding: 0.6%;
	padding-right: 2%;
	cursor: pointer;
}

#textAreaDiv {
	margin: 0 auto;
	border: 1px #d1d5db solid;
	border-radius: 4px;
	padding: 1%;
}

#txtArea {
	width: 100%;
	height: 30%;
	font-size: 14px;
	font-weight: 400;
	font-family: "Inter", sans-serif;
	color: #585d68;
	line-height: 1.2em;
	resize: none;
	outline: none;
	border: none;
}

.resultsDiv {
	margin: 0 auto;
}

#resultsDivTitle {
	font-size: 14px;
	color: #374151;
	padding-bottom: 11px;
	position: sticky;
	top: 0;
	background-color: white;
	font-weight: 500;
}

#resultsDivTitle span {
	float: right;
	color: #9ca3af;
	font-weight: 400;
}

table {
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	border-spacing: 0;
}

#rangesTable {
	width: 100%;
}

#rangesTable th {
	font-size: 16px;
	font-weight: 500;
	color: #5b21b6;
	text-align: left;
	padding: 4%;
	background-color: #ede9fe;
	position: sticky;
}

#rangesTable th:first-child {
	border-radius: 4px 0 0 4px;
}

#rangesTable th:last-child {
	border-radius: 0 4px 4px 0;
}

#rangesTable td {
	font-size: 14px;
	font-weight: 400;
	color: #6b7280;
	padding: 4%;
	border-bottom: 1px solid #dfdfdf;
}

@media only screen and (max-width: 425px) {
	.textDiv,
	.resultsDiv {
		width: 94%;
		max-width: 375px;
	}

	.resultsDiv {
		margin-top: 6%;
		margin-bottom: 20%;
	}

	#resultsDivTitle {
		padding-top: 10px;
	}

	#rangesTable th {
		top: 38px;
	}
}

@media only screen and (min-width: 426px) and (max-width: 767px) {
	.textDiv,
	.resultsDiv {
		width: 92%;
		max-width: 600px;
	}

	.resultsDiv {
		margin-top: 6%;
		margin-bottom: 20%;
	}

	#resultsDivTitle {
		padding-top: 10px;
	}

	#rangesTable th {
		top: 38px;
	}
}

@media only screen and (min-width: 768px) {
	header {
		padding-top: 1%;
		padding-bottom: 5%;
	}

	/* Split the screen in half */
	.textDiv {
		width: 48.5%;
		margin-left: 1%;
		position: absolute;
		bottom: 1%;
		height: calc(100vh - 160px);
	}

	.resultsDiv {
		margin-top: 0;
		width: 48.5%;
		margin-right: 1%;
		position: absolute;
		bottom: 1%;
		height: calc(100vh - 160px);
		/* border: 1px solid red; */
		overflow-y: auto;
	}

	#textAreaDiv {
		position: relative;
		bottom: -1%;
	}

	#txtArea {
		height: calc(100vh - 210px);
	}

	#resultsDivTitle {
		padding-top: 0px;
	}

	#rangesTable {
		margin-bottom: 50px;
	}

	#rangesTable th {
		top: 28px;
	}

	/* Control the left side */
	.left {
		left: 0;
	}

	/* Control the right side */
	.right {
		right: 0;
	}
}

